@import url("https://fonts.googleapis.com/css2?family=Bungee+Inline&display=swap");

body {
  box-sizing: border-box;
  margin: 0;
  font-family: "Bungee Inline", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1 {
  margin: 0;
}

img {
  display: block;
  width: 100%;
}
